<template>
	<div>
		<el-card class="box-card">
			<el-row :gutter="20">
				<el-col :span="12">
					<el-input v-model="input" placeholder="请输入内容" clearable @keyup.enter.native="addlItem"></el-input>
				</el-col>
			</el-row>
       <el-checkbox-group v-model="checkeditem">
				<el-row v-for="item in datalist" :key="item.name">
          <el-col :span="13">
               <el-checkbox :label="item.name">	{{ item.name }}</el-checkbox>
          </el-col>
         <el-col :span="11">
            <el-button type="primary" icon="el-icon-edit" circle
           @click="updateData(item.name)"></el-button>
            <el-button type="primary" @click="removeitem(item.name)">删除</el-button>
         </el-col>
				</el-row>
       </el-checkbox-group>
		</el-card>
	</div>
</template>

<script>
export default {
	name: 'power',
	components: {},
	mixins: [],
	props: {},
	data() {
		return {
			input: '',
			datalist: [
				{
					name: '小杰',
				},
				{
					name: '小徐',
				},
				{
					name: '徐文杰',
				},
				{
					name: '小明',
				},
			],
		checkeditem:[

    ],
		}
	},
	computed: {},
	watch: {},
	created() {
    const aa = {
      ad:'小雅'
    }
    const bb = null
   // undefined属性未定义未初始化值
    //console.log(aa.name);
     console.log(bb);
    //命名路由接收动态参数
    // console.log(this.$route.parmas.name,'传参');
    //path,query路由
    console.log(this.$route.query.name,'传参');
  },
	mounted() {},
	destroyed() {},
	methods: {
    //添加
    addlItem() {
       if(this.input!='') {
         const obj = {}
         obj.name = this.input
         this.datalist.push(obj)
       }
    },
    //删除
    removeitem(name) {
     let id = this.datalist.findIndex(item=>{
        return item.name === name
      })
      if(this.checkeditem.indexOf(name)!=-1)
        this.datalist.splice(id,1)
      },
        //修改
    updateData(name) {
      const obj = {name:'笑傲江湖'}
       let id = this.datalist.findIndex(item=>{
        return item.name === name
      })
       // this.datalist.splice(0,1,obj)
        this.$set(this.datalist,id,obj)
    }
  },


}
</script>
<style lang='less' scoped>

  .el-row {
    margin-top: 15px;
  }
  .el-checkbox-group .el-row {
    height: 40px;
    line-height: 40px;

  }

</style>
